import React, {useState} from "react";
import s2 from "../../p1-main/m1-ui/u1-app/App.module.css";
import s from "./HW13.module.css";
import SuperButton from "../h4/common/c2-SuperButton/SuperButton";
import s3 from "../h4/common/c2-SuperButton/SuperButton.module.css";
import axios from "axios";
import success200 from "./images/200.svg";
import error400 from "./images/400.svg";
import error500 from "./images/500.svg";
import errorUnknown from "./images/error.svg";

/*
 * 1 -   send
 * 2 -     
 * 3 -      
 * */

const HW13 = () => {
    const [code, setCode] = useState("");
    const [text, setText] = useState("");
    const [info, setInfo] = useState("");
    const [image, setImage] = useState("");
    const [dis, setDis] = useState(false);

    const send = (x?: boolean | null) => () => {
        const url =
            x === null
                ? "https://xxxxxx.ccc" //      
                : "https://samurai.it-incubator.io/api/3.0/homework/test";

        setCode("");
        setImage("");
        setText("");
        setInfo("...loading");
        setDis(true);

        axios
            .post(url, {success: x})
            .then((res) => {
                setCode(" 200!");
                setImage(success200);
                setInfo(res.data.info);
            })
            .catch((e) => {
                if (e.response) {
                    if (e.response.status === 400) {
                        setCode(" 400!");
                        setText(e.response.data.errorText);
                        setInfo(e.response.data.info);
                        setImage(error400);
                    } else if (e.response.status === 500) {
                        setCode(" 500!");
                        setText(e.response.data.errorText);
                        setInfo(e.response.data.info);
                        setImage(error500);
                    } else {
                        setImage(errorUnknown);
                        setText("Some unhandled respons status");
                    }
                } else if (e.request) {
                    console.log(e.request)
                    setText("Axios Network Error");
                    setInfo("Please try another request");
                    setImage(errorUnknown);
                } else {
                    setText("Some unhandled error");
                }
            })
            .finally(() => setDis(false));
    };

    return (
        <div id={"hw13"}>
            
            <div className={s2.hwTitle}>Hometask  13</div>
            <hr />
            <div className={s2.hw}>
                <div className={s.buttonsContainer}>
                    <SuperButton
                        id={"hw13-send-true"}
                        xType={s3.default}
                        disabled={dis}
                        onClick={send(true)}
                        // 
                    >
                        Send true
                    </SuperButton>
                    <SuperButton
                        id={"hw13-send-false"}
                        onClick={send(false)}
                        xType={s3.default}
                        disabled={dis}
                        // 
                    >
                        Send false
                    </SuperButton>
                    <SuperButton
                        id={"hw13-send-undefined"}
                        onClick={send(undefined)}
                        xType={s3.default}
                        disabled={dis}
                        // 
                    >
                        Send undefined
                    </SuperButton>
                    <SuperButton
                        id={"hw13-send-null"}
                        onClick={send(null)} //      
                        xType={s3.default}
                        disabled={dis}
                        // 
                    >
                        Send null
                    </SuperButton>
                </div>

                <div className={s.responseContainer}>
                    <div className={s.imageContainer}>
                        {image && <img src={image} className={s.image} alt="status" />}
                    </div>

                    <div className={s.textContainer}>
                        <div id={"hw13-code"} className={s.code}>
                            {code}
                        </div>
                        <div id={"hw13-text"} className={s.text}>
                            {text}
                        </div>
                        <div id={"hw13-info"} className={s.info}>
                            {info}
                        </div>
                    </div>
                </div>
            </div>
            <hr/>
        </div>
    );
};

export default HW13;
